/* @flow */

import styled from 'styled-components';
import React from 'react';
import Link from './Link';
import followOnFacebookImg from '../socialMedia/folow_on_facebook.jpg';

export const Title = styled.h1`
  color: #362c2a;
  font-size: 36px;
  margin-top: 20px;
`;

export const SubTitle = styled.h1`
  color: #362c2a;
  font-size: 26px;
  margin-top: 20px;
`;

export const Para = styled.p`
  color: #362c2a;
  margin-bottom: 26px;
  font-size: 16px;
  line-height: 1.625;
`;

export const TextWrapper = styled.div`
  max-width: 720px;
  margin: 0 auto;
  overflow: hidden;
  margin-bottom: 10px;
`;

export const Li = styled.li`
  color: #362c2a;
  line-height: 1.4;
  margin: 5px;
`;

export const ContentCTA = styled(Link)`
  display: inline-block;
  text-align: center;
  background: #e72f34;
  background: linear-gradient(to bottom, rgba(235,73,78,1) 0%, rgba(231,47,53,1) 100%);
  padding: 10px;
  border-radius: 4px;
  color: white;
  text-decoration: none;
  font-weight: bold;
  font-style: italic;
  font-variant: small-caps;
  font-size: 22px;
  box-shadow: 0px 6px #9e1e21;
  text-align: center;
  transition: 0.3s all;
  margin-bottom: 16px;
  
  &:hover {
    box-shadow: 0px 3px #9e1e21;
    transform: translate(0px, 3px);
  }
  
  &:active {
    box-shadow: 0px 1px #9e1e21;
    transform: translate(0px, 5px);
  }
`;

export const ContentSecCTA = ContentCTA.extend`
  font-size: 20px;
  font-weight: normal;
  box-shadow: 0px 4px #9e1e21;
  margin-bottom: 14px;
  padding: 7px;
  
  &:hover {
    box-shadow: 0px 2px #9e1e21;
    transform: translate(0px, 2px);
  }
  
  &:active {
    box-shadow: 0px 1px #9e1e21;
    transform: translate(0px, 3px);
  }
`;

export const CenterInline = styled.div`
  text-align: center;
`;

const FollowOnFacebookImage = styled.img`
  width: 100%;
  margin: 20px 0;
`;

export const FollowOnFacebook = () => (
  <Link href="https://www.facebook.com/FreeReikiCourseOfficial/">
    <FollowOnFacebookImage src={followOnFacebookImg}/>
  </Link>
);



